
import { defineComponent, onMounted, reactive } from "@vue/runtime-core";
import draggable from "vuedraggable";
import * as icons from "@element-plus/icons";
interface IconItemType {
  label: string;
  icon: string;
  id: number;
  color: string;
}
export default defineComponent({
  name: "CardDraggable",
  components: { draggable },
  setup() {
    const iconList = Object.keys(icons).splice(0, 50);
    const list: Array<IconItemType> = reactive([]);
    const drag = false;
    const colors = [
      "#e54d42",
      "#f37b1d",
      "#fbbd08",
      "#8dc63f",
      "#39b54a",
      "#1cbbb4",
      "#0081ff",
      "#6739b6",
      "#9c26b0",
      "#e03997",
      "#a5673f",
      "#8799a3",
    ];
    const getRandomColor = (): string => {
      return colors[Math.floor(Math.random() * colors.length)];
    };
    const resetElements = (): void => {
      list.sort((a: IconItemType, b: IconItemType) => {
        return a.id - b.id;
      });
    };
    onMounted(() => {
      list.push(
        ...iconList.map((it: string, index: number): IconItemType => {
          return {
            label: `第 ${index + 1} 个元素`,
            icon: it,
            id: index,
            color: getRandomColor(),
          };
        })
      );
    });
    return {
      drag,
      list,
      resetElements,
    };
  },
});
